.Background {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgba(0,0,0,0.5);
}

.SummaryPopup {
  position: absolute;
  top: 5%;
  left: 5%;
  right: 5%;
  bottom: 5%;
  background: white;
  border-radius: 20px;
  border: 2px solid #C0C0C0;
  box-shadow: 4px 4px #C0C0C0;
}

.SummaryPopupHeader {
  position: absolute;
  top: 5px;
  right: 0;
  left: 0;
  font-size: 40px;
  color: #C0C0C0;
}

.PromptBox {
  position: relative;
  display: flex;
  text-align: left;
  margin-top: 10px;
  top: 65px;
  left: 5%;
  width: 90%;
  height: 25%;
  border: 2px solid #C0C0C0;
  border-radius: 10px;
}

.PromptColumn {
  padding: 5px;
  left: 0;
  width: 24%;
  overflow: scroll;
}

.PromptColumn h1 {
  text-align: center;
  font-size: 12px;
  color: #C0C0C0
}

.ResponseColumn {
  padding: 5px;
  border-left: 1px solid #C0C0C0;
  left: 24%;
  width: 24%;
  overflow: scroll;
}

.ResponseColumn h1 {
  text-align: center;
  font-size: 12px;
  color: #C0C0C0
}

.ExamplesColumn {
  padding: 5px;
  border-left: 1px solid #C0C0C0;
  left: 48%;
  width: 24%;
  overflow: scroll;
}

.ExamplesColumn h1 {
  text-align: center;
  font-size: 12px;
  color: #C0C0C0
}

.ExamplesColumn ul {
  display: list-item;
  padding: 0;
  margin: 0;
  list-style-type: disc;
  list-style-position: inside;
}

.ButtonsColumn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px;
  border-left: 1px solid #C0C0C0;
}

.Button {
  display: flex;
  align-items: center;
  justify-content: center;
  user-select: none;
  height: 50px;
  width: 50px;
  margin: 14px;
  border: 2px solid #C0C0C0;
  border-radius: 15px;
  box-shadow: 1px 1px #C0C0C0;
  color: #C0C0C0;
  font-size: 13px;
}

.Button:hover {
  cursor: pointer;
}

.Button:active {
  border: 2px solid white;
  box-shadow: 0px 0px;
  font-size: 12px;
}

.Button:focus {
  outline: 0;
}

.Green {
  background: #d2e7d2;
}

.Yellow {
  background: #FFEBCD;
}

.Red {
  background: #FFDAB9;
}
