.TranslateButton {
  position: absolute;
  display: inline-block;
  line-height: 160px;
  top: 0;
  left: 45%;
  right: 45%;
  bottom: 0;

  background-color: inherit;
}

.ArrowIcon {
  color: white;
  font-size: 50px;
  outline: 1px solid #D3D3D3;
}

.ArrowIcon:hover {
  color: #C0C0C0;
  cursor: pointer;
}
