.TargetOutput {
  position: absolute;
  display: flex;
  align-items: center;
  top: 0;
  right: 0;
  bottom: 0;
  width: 45%;
  background-color: inherit;
}

.TargetOutputField {
  background: white;
  border: 3px solid white;
  border-radius: 5px;
  position: absolute;
  right: 20%;
  width: 70%;
  height: 105px;
  color: black;
  font-size: 20px;
  line-height: 175%;
  overflow: scroll;
}

.HiddenField {
  background: #D3D3D3;
}

.TranslationLine {
  text-align: left;
}

.TranslationWord {
  width: 70%;
  display: inline-block;
}

.QualityOuter {
  bottom: 0;
  right: 0;
  background: inherit;
  height: 5px;
  width: 28%;
  display: inline-block;
  border-radius: 5px;
  border: 2px solid #D3D3D3;
}

.QualityScore {
  background: #FFEBCD;
  height: inherit;
  border-radius: inherit;
}
