.LanguageMenu {
  position: absolute;
  display: flex;
  align-items: center;
  top: 0;
  left: 0;
  bottom: 0;

  width: 25%;

  background-color: white;
}

.LanguageIcon {
  position: absolute;
  left: 10%;
  color: #D3D3D3;
  font-size: 50px;
}

.LanguageIcon:hover {
  color: #C0C0C0;
  cursor: pointer;
}
