.Prompt {
  position: absolute;
  top: 0;
  left: 25%;
  right: 25%;
  height: 75%;

  font-size: 50px;
  padding-top: 100px;

  text-align: center;
}

.WordBox {
  position: relative;
  font-size: 40px;
}

.WordBox:hover {
  border-bottom: 1px solid black;
  cursor: pointer;
}

.WordBox:hover span{
  visibility: visible;
}

.TransBox {
  visibility: hidden;
  font-size: 20px;
  text-align: left;
  position: absolute;
  padding: 5px;
  border: 1px solid #D3D3D3;
  border-radius: 10px;
  width: 300px;
  height: 80px;
  bottom: 100%;
  left: 0;
  background: white;
  z-score: 1;
  overflow: scroll;
}

.TranslationWord {
  width: 70%;
  display: inline-block;
}

.QualityOuter {
  bottom: 0;
  right: 0;
  background: inherit;
  height: 5px;
  width: 28%;
  display: inline-block;
  border-radius: 5px;
  border: 2px solid #D3D3D3;
}

.QualityScore {
  background: #FFEBCD;
  height: inherit;
  border-radius: inherit;
}
