.SourceInput {
  position: absolute;
  display: flex;
  align-items: center;
  top: 0;
  left: 0;
  bottom: 0;
  width: 45%;
  background-color: inherit;
}

.SourceInputField {
  border: 3px solid #D3D3D3;
  border-radius: 5px;
  position: absolute;
  left: 50%;
  width: 40%;
  height: 35px;
  color: black;
  font-size: 20px;
}

.SourceInputField:focus {
  outline: none;
  border: 3px solid #C0C0C0;
  transition: .3s ease-in;
}

.SourceInputField::placeholder {
  text-align: center;
  color: #D3D3D3;
  font-style: italic;
}

.SourceInputField:focus::-webkit-input-placeholder {
    opacity: 0;
}
