.Background {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgba(0,0,0,0.5);
}

.HelpPopup {
  position: absolute;
  top: 5%;
  left: 5%;
  right: 5%;
  bottom: 5%;
  background: white;
  border-radius: 20px;
  border: 2px solid #C0C0C0;
  box-shadow: 4px 4px #C0C0C0;
}

.HelpPopupHeader {
  position: absolute;
  top: 5px;
  right: 0;
  left: 0;
  font-size: 40px;
  color: #C0C0C0;
}

.HelpPopupVideo {
  position: absolute;
  text-align: center;
  bottom: 10%;
  height: 70%;
  left: 10%;
  width: 80%;
}

.ReactPlayer {
  display: inline-block;
}
