.Background {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgba(0,0,0,0.5);
}

.LanguagePopup {
  position: absolute;
  top: 25%;
  left: 25%;
  right: 25%;
  bottom: 25%;
  background: white;
  border-radius: 20px;
  border: 2px solid #C0C0C0;
  box-shadow: 4px 4px #C0C0C0;
}

.LanguagePopupHeader {
  position: absolute;
  top: 5%;
  right: 0;
  left: 0;
  font-size: 40px;
  color: #C0C0C0;
}

.LanguageInput {
  border: 2px solid #D3D3D3;
  border-radius: 5px;
  position: absolute;
  left: 14%;
  width: 60%;
  bottom: 35%;
  height: 55px;
  color: black;
  font-size: 30px;
}

.LanguageInput:focus {
  outline: none;
  border: 2px solid black;
  transition: .3s ease-in;
}

.LanguageInput::placeholder {
  text-align: left;
  color: #D3D3D3;
  font-style: italic;
}

.SubmitLanguage {
  color: black;
  font-size: 62px;
  position: absolute;
  bottom: 35%;
  right: 14%;
}

.SubmitLanguage:hover {
  cursor: pointer;
}
