.ProgressBar {
  position: absolute;
  display: flex;
  align-items: center;
  top: 0;
  bottom: 0;

  left: 25%;
  right: 25%;

  background-color: white;
}

.Outer {
  position: relative;
  height: 5%;
  width: 100%;
  border-radius: 20px;
  border: 2px solid #D3D3D3;
}

.Inner {
  background-color: #d2e7d2;
  height: 100%;
  border-radius: inherit;
  transition: width .2s ease-in;
}
