.StartingPopupBackground {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: #D3D3D3;
}

.StartingPopup {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 4% 0;
  top: 25%;
  left: 25%;
  right: 25%;
  bottom: 25%;
  background: white;
  border-radius: 20px;
  border: 2px solid white;
  box-shadow: 4px 4px white;
}

.TargetLanguageButton {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 20px;
  margin-right: 20px;
  width: 250px;
  height: 200px;
  border: 2px solid #C0C0C0;
  box-shadow: 3px 3px #C0C0C0;
  border-radius: 20px;
}

.TargetLanguageButton p {
  position: absolute;
  top: 30%;
}

.TargetLanguageButton h1 {
  text-decoration: underline;
  font-weight: normal;
}

.TargetLanguageButton:hover {
  cursor: pointer;
}

.TargetLanguageButton:active {
  border: 2px solid #C0C0C0;
  box-shadow: inset 1px 1px #C0C0C0;
}
