.Response {
  background-color: inherit;
  position: absolute;
  bottom: 0;
  left: 25%;
  right: 25%;
  height: 25%;
}

.InputField {
  position: absolute;
  left: 0;
  width: 90%;
  height: 30px;
  font-size: 25px;

  border: 0;
  outline: 0;
  background: transparent;
  border-bottom: 2px solid #D3D3D3;

  font-family: inherit;
}

.InputField:focus {
  border-bottom: 2px solid black;
  transition: .3s ease-in;
}

.SubmitResponse {
  position: absolute;
  right: 0;
  font-size: 30px;
  color: #D3D3D3;
}

.SubmitResponse:hover {
  color: black;
  cursor: pointer;
}
